.defaultview {
    position: relative;
    min-height: 86vh;
    padding: 0 24px 24px;
    margin: 0;
}

h1 {
    text-align: center;
}

p {
    text-align: justify;
}

@media all and (max-width: 480px) {
    h1 {
        font-size: 6vw;
    }

    p {
        font-size: 3vw;
    }
}

.Modal {
    height: 90%;
    margin: 5%;
    max-width: 400px;
    position: absolute;
}


